<template>
  
  <div class="modal fade" id="freightSearchSimpleModal" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-body">

        <div class="input-group mb-3">
          <input type="text" class="form-control" placeholder="请输入菜品名称" v-model="named" id="named" name="named">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="searchCancel">清空</button>
            <button class="btn btn-outline-info" type="button" id="named" @click="searchSubmit">搜索</button>
          </div>
        </div>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  name: 'FreightSearchSimpleModal',
  data() {
    return {
      named: '',
      search_f: false
    }
  },
  methods: {
    searchSubmit() {
      let res = validate.val_empty(this.named, '菜品名称')
      if (res == true) {
        this.search_f = true
        this.$emit('reciverFreightNamed_Father', this.named)
      } else {
        this.searchCancel()
      }
    },
    searchCancel() {
      this.named = ''
      if (this.search_f) {
        this.$emit('reciverFreightNamed_Father', null)
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


